import React, { useEffect, useState } from 'react';
import logoUrl from '../../../assets/img/html.png';
import store from './redus/state';


const Title = () => {
    return (
        <div className='title'>
            <h1>Тест</h1>
        </div>
    )
}

const Logo = () => {
    return (
        <div className='main-intro__logo'>
            <img className= 'logo' src = {logoUrl} alt="logo"></img>
        </div>
    )
}
const Intro = () => {
    return (
        <div className='main-intro__text'>
            <h2>Тест по HTML</h2>
            <div className='main-intro__description'>
                <p>Описание<strong>&ensp; 20 вопросов по основам HTML.</strong> При очередном прохождении теста многие вопросы будут заменены. За каждый правильный ответ вы получаете 1 балл, в конце каждого теста вы получаете свой общий балл.</p>
            </div>
        </div>
    )
}

const MainIntro = () => {
    return (
        <div className='main-intro'>
            <Logo/>
            <Intro/>
        </div>
    )
}

const NumberQueastion = () => {
    return (
        <div className='main-question__quantity'>
            Вопрос 
            <span className="current">&ensp;1</span> 
            <span> из </span>
            <span className="total">20</span>
        </div>
    )
}

const Circle = () => {
    return (
        <div className='circle'>
            <div className='circle__target' key='1'/>
            <div className='circle__target' key='2'/>
            <div className='circle__target' key='3'/>
            <div className='circle__target' key='4'/>
            <div className='circle__target' key='5'/>
            <div className='circle__target' key='6'/>
        </div>
    )
}

const TextQueastion = (props) => {
    return (
        <div className='main-question__text'>
            <p>
                <span>1. </span>
                <span>{props.text}</span>
            </p>
        </div>
    )
}
const Question = (props) => {

    return (
        <div className='main-question'>
            <NumberQueastion/>
            <Circle/>
            <TextQueastion text={props.text}/>
        </div>
    )
}

const Header = (props) => {
    return (
        <div className='main'>
            <Title/>
            <MainIntro/>
            <Question text={props.state.questions.item[0].text}/>
        </div>
    )
}

const Options = (props) => {
    let index = props.state.number - 1;
    const addResponse = (e) => {
        let textAnswer = e.target.value;
        let newAnswer = {
            id: props.state.number, 
            text: textAnswer 
        }
        props.versionAnswer(newAnswer);
    }
    return (
      <ul className='answers'>
        <li>
            <input id="quest062" name="question" type="radio" 
                onChange={addResponse} value={props.state.questions.item[index].var[0]} /> 
            <label htmlFor="quest062">{props.state.questions.item[index].var[0]}</label>
        </li>
        <li>
            <input id="quest162" name="question" type="radio" 
                onChange={addResponse} value={props.state.questions.item[index].var[1]}/> 
            <label htmlFor="quest162">{props.state.questions.item[index].var[1]}</label>
        </li>
        <li>
            <input id="quest262" name="question" type="radio" 
                onChange={addResponse} value={props.state.questions.item[index].var[2]}/> 
            <label htmlFor="quest262">{props.state.questions.item[index].var[2]}</label>
        </li>
        <li>
            <input id="quest362" name="question" type="radio" 
                onChange={addResponse} value={props.state.questions.item[index].var[2]}/> 
            <label htmlFor="quest362">{props.state.questions.item[index].var[3]}</label>
        </li>
      </ul>
  )
}

const Control = (props) => {
    const onwards = () => {
      props.onClickParent(); //вызываем обработчик событий их Родителя
      props.addAnswer(); //увеличиваем номер вопроса в state
    }

    return (
      <div className="button next-question" onClick={onwards} >Дальше»</div>
    )
}

function App(props) {

    let [compl, setEditMode] = useState(true);
    let [_state, setStatus] = useState(props.state);

    useEffect( ()  => {
        setStatus(props.state);
    }, [props.state])

    const onClick = () => {
        if(compl)  {setEditMode(false) }  else {setEditMode(true)}
    }

	return (
    <>
      <div className="app-wrapper">
            <Header state={_state}/>
            <Options state={_state} versionAnswer = {props.versionAnswer}/>
            <Control number={props.number} addAnswer = {props.addAnswer} onClickParent={onClick}/>
      </div>
    </>
	);
}



const FragmentComponent = () => {
    return (
        <App state={store.state}
            versionAnswer={store.versionAnswer.bind(store)}
            addAnswer = {store.addAnswer.bind(store)}
        />
    )
}
export default FragmentComponent;
