import React, { Fragment } from 'react';

let fragmentIndex = (
<Fragment>
<div className="app-wrapper">
    <div className="main">
      <div className="title">
        <h1>Тест</h1>
      </div>
      <div className="main-intro">
        <div className="main-intro__logo">
            <img className="logo" src= {require('../../../assets/img/html.png').default} alt="logo"/>
          </div>
          <div className="main-intro__text">
            <h2>Тест по HTML</h2>
            <div className="main-intro__description">
              <p>Описание<strong> 20 вопросов по основам HTML.</strong> При очередном прохождении теста многие вопросы
                будут заменены. За каждый правильный ответ вы получаете 1 балл, в конце каждого теста вы получаете свой
                общий балл.</p>
            </div>
          </div>
      </div>
      <div>Question</div>
  </div>
</div>
</Fragment>

)
export default fragmentIndex;
