let strHeaderTitle =`
import React from 'react';
import s from './Header.module.css';
import Title from './Title';


const Header = (props) => {
    return (
        <div className={s.main}>
            <Title/>
            <div>MainIntro</div>
            <div>Question</div>
        </div>
    )
}

export default Header;
`

export default strHeaderTitle;
