import React from "react";
import s from './StepFour.module.css';
import AppView from "./AppView/AppView";
import unnecessaryUrl from '../../assets/img/Step4_unnecessary_files.jpg';
import editUrl from '../../assets/img/Step4_editor.jpg';
import FileAppCss from "./FileAppCss/FileAppCss";
import FileAppJs from "./FileAppJs/FileAppJs";
import FileIndexJs from "./FileIndexJs/FileIndexJs";

const StepFour = () => {
    return (
        <>
            <p>Откладываю верстку index.html и style.css в сторону. К ней вернусь позже </p>

            <p> Читаю: <a href='https://ru.react.js.org/docs/create-a-new-react-app.html' target="_blank" rel="noreferrer">https://ru.react.js.org/docs/create-a-new-react-app.html</a> и делаю как написано</p>
            <div className={s.gatsbyHighlight}>
                <pre>
                    <code className={s.gatsbyCode}>
                        <div className={s.gatsbyText}>
                            npx create-react-app my-app
                        </div>
                        <div className={s.gatsbyText}>
                            <span className={s.token}>cd</span> my-app
                        </div>
                        <div className={s.gatsbyText}>
                            <span className={s.tokenNpm}>npm</span> start
                        </div>
                    </code>
                </pre>
            </div>
            <br/>
            <AppView/>
            <p>Удаляю все лишнее в приложении React:</p>
            <div className={s.containerImg}>
                <img className={s.img_wrapper} alt="Ненужные файлы" src={unnecessaryUrl}/>
                <span className={s.text}> Должно остаться это: </span>
                <img className={s.img_wrapper} alt="Редактор" src={editUrl}/>
            </div>
            <p>Редактирую файлы</p>
            <FileAppCss/>
            <FileAppJs/>
            <FileIndexJs/>
        </>
    )
}

export default StepFour;