let strState62 =`
let store = {
  state: {
    questions: {
      item: [
      {
        id:1,
        text: 'Как открыть ссылку в новой вкладке / окне браузера?',
        var: [
          '<a href="url" target="_blank">Текст</a>',
          '<a href="url" new>Текст</a>',
          '<a href="url" target="new">Текст</a>',
          '<a href="url">Текст</a>'
        ]
      },
      {
        id:2,
        text: 'Какие из этих тегов относятся к элементу <table>?',
        var: [
          '<table><tr><td>',
          '<table><tr><tt>',
          '<table><head><tfoot>',
          '<thead><body><tr>'
        ]
      }
    ]
  },
    answer: [],
    number: 1
  },
  versionAnswer (answer) {
      this.state.answer = answer;
      console.log(this.state.answer);
  },
  addAnswer () {
    if (this.state.number < this.state.questions.item.length) {
      this.state.number++;
    } else {
        this.state.number = 1;
    }
    this._renderEntireTree(this.state);
  },
  _renderEntireTree() {
    console.log('Вызов state');
  },
  subscribe (observer){
      this._renderEntireTree = observer;
  }
};

export default store;
`
export default strState62;
