let strHeaderCSS =`
.main {
  display: flex;
  flex-direction: column;
  margin-top: 2%;
  font-size: 1.2em;
}
.title {
  margin: 0 2% 2%;
}
@media (max-width: 800px) {
  .title {
    font-size: calc(var(--index) * 1.2 );
  }
}
@media (max-width: 800px) and (min-aspect-ratio: 128/128) {
  .title {
    font-size: calc(var(--index) * 1.2 );
  }
}
.mainIntro {
  display: flex;
  justify-content: space-between;
  flex: 30%;
  margin-top: 2%;
  padding: 1% 2%;
}
@media (max-width: 800px) {
  .mainIntro {
    flex-direction: column
  }
}
@media (max-width: 800px) and (min-aspect-ratio: 128/128) {
  .mainIntro {
    flex-direction: column
  }
}
.mainIntro__logo {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.mainIntro__logo > .logo {
  max-width: 128px;
  width: auto;
  height: auto;
}
@media  (max-width: 800px) {
  .mainIntro__logo > .logo {
    width: calc(var(--index) * 8 );
  }
}
@media (max-width: 800px) and (min-aspect-ratio: 128/128) {
  .mainIntro__logo > .logo {
    width: calc(var(--index) * 8 );
  }
}
.mainIntro__text {
  flex-grow: 2.5;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 1% 2%;
}
@media  (max-width: 800px) {
  .mainIntro__text {
    font-size: calc(var(--index) * 1.2 );
  }
}
@media (max-width: 800px) and (min-aspect-ratio: 128/128) {
  .mainIntro__text {
    font-size: calc(var(--index) * 1.2 );
  }
}
@media  (max-width: 400px) {
  .mainIntro__text {
    font-size: 12px;
  }
}
@media (max-width: 400px) and (min-aspect-ratio: 300/128) {
  .mainIntro__text {
    font-size: 12px;
  }
}
.mainQuestion {
  padding: 2% 2% 0 2%;
}
.mainQuestion__text {
  margin-top: 1%;
}
.circle {
  display: flex;
  gap: 1%;
}
.circle__target {
  border-radius:50%;
  width: 24px;
  height: 24px;
  background-color: white;
  border:1px solid black;
}
.circleGadget {
  display: flex;
  justify-content: space-around;
}
.circleGadget__target:nth-child(1) {
  display: flex;
  justify-content: center;
  border-radius:50%;
  width: 30px;
  height: 30px;
  background-color: #00B28E;
  border:1px solid black;
  align-content: center;
  flex-wrap: wrap;
}
.circleGadget__target:nth-child(2) {
  display: flex;
  justify-content: center;
  border-radius:50%;
  width: 30px;
  height: 3030;
  background-color: red;
  border:1px solid black;
  align-content: center;
  flex-wrap: wrap;
}
.true  {
  background-color: #00B28E;
}
.false  {
  background-color: red;
}
.mainQuestion__quantity {
  margin-bottom: 1%;
}
`

export default strHeaderCSS;
