import React from 'react';
import s from './StepTwelve.module.css';

const StepTwelve = () => {
    return (
        <>
            <p>В своем аккауте  <b>GitHub</b> создаю проект с именем test</p>
            <p>У себя VSCode пишу</p>
            <div className={`${s.background} ${s.blockCode} ${s.decor}`}>
                <code className={s.colorLightBlue}>{`>`} git clone https://github.com/shal-work/test.git</code>
            </div>
            <p>Перехожу в каталок <b>test</b></p>
            <div className={`${s.background} ${s.blockCode} ${s.decor}`}>
                <code className={s.colorLightBlue}>{`>`} cd .\test\</code>
            </div>
            <p>переношу код из <b>build</b> в папку <b>test</b></p>

            <div className={`${s.background} ${s.blockCode} ${s.decor}`}>
                <code className={s.colorLightBlue}>{`>`} git add –A</code>
                <code className={s.colorLightBlue}>{`>`} git commit –a –m”first commit”</code>
                <code className={s.colorLightBlue}>{`>`} git push</code>
            </div>
            <p>На GitHub появятся файлы проекта</p>
            <p>На GitHub выходим в Setting находим пункт Page. В пункте Branch выбираем <b>main</b> {`(`}вместо <b>None</b>{`)`} и нажимаем Save. Через некоторое время в разделе GitHub Pages появится ссылка «Ваш сайт доступен по адресу:&nbsp;
            <a href='https://shal-work.github.io/test/' target='_blank' rel="noreferrer">https://shal-work.github.io/test/</a></p>
            <h2>ВСЁ</h2>
        </>
    )
}

export default StepTwelve;