import React from 'react';
import s from './StoreApp.module.css';
import str1 from './strStoreApp1';
import str2 from './strStoreApp2';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter'
import { oneDark } from 'react-syntax-highlighter/dist/esm/styles/prism';

const StoreApp = (props) => {

    let valueStr = str1;
    if (props.item === 60) {
        // console.log(data.item );
        valueStr = str1;
    }
    if (props.item === 61) {
        // console.log(data.item );
        valueStr = str2;
    }

    return (
        <div className={s.container}>
            <div  className= {'code '}>
                <div className='containerSyntaxhigHlighter'>
                    <SyntaxHighlighter language="jsx" style={oneDark} wrapLongLines={true}>
                        {valueStr}
                    </SyntaxHighlighter>
                </div>
            </div>
        </div>
    )
}

export default StoreApp;
