import React from 'react';
import s from './StoreQuestion.module.css';
import str from './strStoreQuestion';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { oneDark } from 'react-syntax-highlighter/dist/esm/styles/prism';

const StoreQuestion = () => {
    return (
        <div className={s.container}>
            <div  className= {'code '}>
                <div className='containerSyntaxhigHlighter'>
                    <SyntaxHighlighter language="jsx" style={oneDark} wrapLongLines={true}>
                        {str}
                    </SyntaxHighlighter>
                </div>
            </div>
        </div>
    )

}

export default StoreQuestion;
