import React, { useEffect, useState } from 'react';
import logoUrl from '../../../assets/img/html.png';
import {versionAnswer, getQuantity, addAnswer, getGo, shuffleOptions, getState} from './redus/store';
import './styleStep9.css';

const Title = () => {
    return (
        <div className='title'>
            <h1>Тест</h1>
        </div>
    )
}

const Logo = () => {
    return (
        <div className='main-intro__logo'>
            <img className= 'logo' src = {logoUrl} alt="logo"></img>
        </div>
    )
}
const Intro = (props) => {
    return (
        <div className='main-intro__text'>
            <h2>Тест по HTML</h2>
            <div className='main-intro__description'>
                <p>Описание<strong>&ensp;{props.getQuantity()} вопросов по основам HTML.</strong> При очередном прохождении теста многие вопросы будут заменены. За каждый правильный ответ вы получаете 1 балл, в конце каждого теста вы получаете свой общий балл.</p>
            </div>
        </div>
    )
}

const MainIntro = (props) => {
    return (
        <div className='main-intro'>
            <Logo/>
            <Intro getQuantity={props.getQuantity}/>
        </div>
    )
}

const NumberQueastion = (props) => {
    return (
        <div className='main-question__quantity'>
            Вопрос 
            <span className="current">&ensp;{props.index}</span> 
            <span> из </span>
            <span className="total">{props.getQuantity()}</span>
        </div>
    )
}

const Circle = (props) => {


    let circleElement = props.answer.map( p => {
        if(!p.viewAnswer) {
            return <div className='circle__target-9' key={p.id}/>
        } else {
            if(p.correctAnswer) {
                return <div className='circle__target-9 true' key={p.id}/>
            } else {
                return <div className='circle__target-9 false' key={p.id}/>
            }
        }
    })
    return (
        <div className='circle'>
            {circleElement}
        </div>
    )
}

const TextQueastion = (props) => {
    return (
        <div className='main-question__text'>
            <p>
                <span>{props.index}. </span>
                <span>{props.text}</span>
            </p>
        </div>
    )
}
const Question = (props) => {

    return (
        <div className='main-question'>
            <NumberQueastion index={props.index} getQuantity={props.getQuantity}/>
            <Circle 
                answer={props.answer} 
                countTrue = {props.countTrue}
                countFalse = {props.countFalse}
            />
            {props.getGo()
                ? <TextQueastion text={props.text} index={props.index}/>
                : <></>
            }
        </div>
    )
}

const Header = (props) => {
    let index = props.state.number - 1;
    return (
        <div className='main'>
            <Title/>
            <MainIntro getQuantity={props.getQuantity}/>
            <Question 
                answer={props.state.answer} 
                text={props.state.questions.item[index].text} 
                index={props.state.number} 
                getGo={props.getGo} 
                getQuantity={props.getQuantity}
                countTrue = {props.state.countTrue}
                countFalse = {props.state.countFalse}
            />
        </div>
    )
}

const Options = (props) => {
    let variant = props.shuffleOptions();
    const addResponse = (e) => {
        let textAnswer = e.target.value;
        let newAnswer = {
            id: props.state.number, 
            text: textAnswer 
        }
        props.versionAnswer(newAnswer);
    }
    useEffect( ()  => {
        if ( props.getGo() ) {
                let inputOptions = document.querySelectorAll(".question-options");
                for (let i =0; i < inputOptions.length; i++) {
                    if (inputOptions[i].checked) {
                        inputOptions[i].checked = '';
                    }
                }
            }
        })
    let variantMap = variant.item.map((v, index) => 
        <li key={index}>
            <input className="question-options" 
                id={'question_9' + index} 
                name='question' 
                type="radio" 
                onChange={addResponse} 
                value={v} />
            <label className='answers' htmlFor={'question_9' + index}>{v}</label>
        </li>
    )
    if( props.getGo()) { 
        return (
            <ul className='answers'>
                {variantMap}
            </ul>
            
        )
    } else {
        return (<div></div>)
    }
}

const Control = (props) => {
    
    const onwards = () => {
      props.onClickParent(); //вызываем обработчик событий их Родителя
      props.addAnswer(); //увеличиваем номер вопроса в state
    }

    return (
      <div className="button next-question" onClick={onwards} >Дальше»</div>
    )
}

function App(props) {

    let [compl, setEditMode] = useState(true);
    let [_state, setStatus] = useState(props.state);

    useEffect( ()  => {
        setStatus(props.state);
    }, [props.state])

    const onClick = () => {
        if(compl)  {setEditMode(false) }  else {setEditMode(true)}
    }

	return (
    <>
      <div className="app-wrapper">
            <Header state={_state} getGo={props.getGo} getQuantity={props.getQuantity}/>
            <Options state={_state} versionAnswer={props.versionAnswer} getGo={props.getGo} shuffleOptions={props.shuffleOptions}/>
            <Control addAnswer={props.addAnswer} getGo={props.getGo} state={_state} onClickParent={onClick}/>
      </div>
    </>
	);
}



const FragmentComponent = () => {
    return (
        <App state={getState}
            versionAnswer={versionAnswer}
            addAnswer = {addAnswer}
            getGo={getGo} 
            shuffleOptions={shuffleOptions}
            getQuantity={getQuantity}
        />
    )
}
export default FragmentComponent;
