let strOptions =`
import React from 'react';
import s from './Options.module.css';

const Options = (props) => {

    return (
            <ul className={s.answers}>
            <li>
                <input id="quest-0" name="question" type="radio"/>
                <label htmlFor="quest0">{props.state.questions.item[0].var[0]}</label>
            </li>
            <li>
                <input id="quest-1" name="question" type="radio"/>
                <label
                    htmlFor="quest-1">{props.state.questions.item[0].var[1]}</label>
            </li>
            <li>
                <input id="quest-2" name="question" type="radio"/>
                <label htmlFor="quest-2">{props.state.questions.item[0].var[2]}</label>
            </li>
            <li>
                <input id="quest-3" name="question" type="radio"/>
                <label htmlFor="quest-3">{props.state.questions.item[0].var[3]}</label>
            </li>
        </ul>
    )
}

export default Options;
`
export default strOptions;
