import React from "react";
import s from "./FileAppJs.module.css"
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter'
import { oneDark } from 'react-syntax-highlighter/dist/esm/styles/prism';

const FileAppJs = () => {

    const text =`
import './App.css';
import React from 'react';

function App() {
    return (
    <div className="App">
        Это начало!
    </div>
    );
}

export default App;`


    return (
        <div className={s.container}>
            <p>В файл App.js:</p>
            <div className="code">
                {/* <code>
                    {text}
                </code> */}
                <div className='containerSyntaxhigHlighter'>
                    <SyntaxHighlighter language="jsx" style={oneDark} wrapLongLines={true}>
                        {text}
                    </SyntaxHighlighter>
                </div>
            </div>
        </div>
    )
}

export default FileAppJs;
