import React, { Fragment } from 'react';
import imgUrl from '../../../assets/img/html.png';


let fragmentIndex = (
<Fragment>
<div className="app-wrapper">
    <div className="main">
      <div className="title">
        <h1>Тест</h1>
      </div>
      <div className="main-intro">
        <div className="main-intro__logo">
          <img className="logo" src={imgUrl} alt="logo"/>
        </div>
        <div className="main-intro__text">
          <h2>Тест по HTML</h2>
          <div className="main-intro__description">
            <span>Описание<strong> 20 вопросов по основам HTML.</strong> При очередном прохождении теста многие вопросы
              будут заменены. За каждый правильный ответ вы получаете 1 балл, в конце каждого теста вы получаете свой
              общий балл.</span>
          </div>
        </div>
      </div>
      <div className="main-question">
        <div className="main-question__quantity"> Вопрос
          <span> 1 </span>
          из
          <span> 20 </span>
        </div>
        <div className="circle">
          <div className="circle__target"></div>
          <div className="circle__target"></div>
          <div className="circle__target"></div>
          <div className="circle__target"></div>
          <div className="circle__target"></div>
          <div className="circle__target"></div>
          <div className="circle__target"></div>
          <div className="circle__target"></div>
          <div className="circle__target"></div>
          <div className="circle__target"></div>
          <div className="circle__target"></div>
          <div className="circle__target"></div>
          <div className="circle__target"></div>
          <div className="circle__target"></div>
          <div className="circle__target"></div>
          <div className="circle__target"></div>
          <div className="circle__target"></div>
          <div className="circle__target"></div>
          <div className="circle__target"></div>
          <div className="circle__target"></div>
        </div>
        <div className="circle-gadget">
          <div className="circle-gadget__target">1</div>
          <div className="circle-gadget__target">2</div>
        </div>
        <div className="main-question__text">
          <span >
            <span>1.</span>
            <span>Какое расширение должны иметь HTML документы?</span>
          </span>
        </div>
      </div>
    </div>
    <ul className="answers">
        <li>
            <input id="quest-0" name="question" type="radio"/> 
            <label htmlFor="quest-0">.html или .htm </label>
        </li>
        <li>
            <input id="quest-1" name="question" type="radio"/> 
            <label htmlFor="quest-1">.txt или .doc</label>
        </li>
        <li>
            <input id="quest-2" name="question" type="radio"/>
            <label htmlFor="quest-2">.php или .asp</label>
        </li>
        <li>
            <input id="quest-3" name="question" type="radio"/>
            <label htmlFor="quest-3">.doc</label>
        </li>
    </ul>
    <a href="#/" className="button next-question" role="button">Дальше »</a>
  </div>
</Fragment>

)
export default fragmentIndex;
