let strMainIntroLogo = `
import React from 'react';
import s from './Header.module.css';
import Logo from './Logo';
import Intro from './Intro';

const MainIntro = () => {
    return (
        <div className={s.mainIntro}>
            <Logo/>
            <Intro/>
        </div>
    )
}

export default MainIntro;
`
export default strMainIntroLogo;
