import React, { Fragment, useState } from 'react';
import s from './StepSecond.module.css';
import imgUrl from '../../assets/img/Step2_design.jpg';

let unhide = (data) => {
    return (
        <>
            <p className={s.btnColor} onClick={data.action}> {data.text}</p>
            <div className={s.containerImg}>
                <img className={`${s.modal} ${data.style}`} alt="Дизайн" src={imgUrl} onClick={data.action}/>
            </div>
            {data.action.name === 'dropUp' &&
                    <p className="color" onClick={data.action}>Скрыть </p>
            }
        </>
    )
}

const StepSecond = () => {
    let [isDown, setEditDown] = useState(false);
    let [source, setEditSource] = useState(true);

    const dropdown = () => {
        setEditDown(true);
    }

    const dropUp = () => {
        setEditDown(false);
    }
    const drop = () => {
        setEditSource(false);
        setEditDown(true);
    }
   
    return (
        <>
            
            {source &&
                <>
                    <p className={s.btnColor} onClick={drop}> Посмотреть дизайн</p>
                </>
            }
            {!isDown && !source &&
                <Fragment>
                    {unhide({action: dropdown, style: `${s.downOut}`, text: 'Посмотреть дизайн'})}
                </Fragment>
            }
            {isDown && !source && 
                <Fragment>
                    {unhide({action: dropUp, style: `${s.growDown}`, text: 'Скрыть'})}

                </Fragment>
            }
        </>
    )
}


export default StepSecond; 