import React, { Fragment, useState } from "react";
import logo from '../../../assets/img/logo.svg';
import s from './AppView.module.css'


let unhide = (data) => {
    return (
        <>
            <p>При запуске команды <b>nmp start</b> появится новое приложение React: &nbsp;&nbsp; <span className={s.btnColor} onClick={data.action}>{data.text} </span> </p>
            <div className={s.container}>
                <div className = {`${s.AppHeader} ${data.style}`}>
                    <img src={logo} className={s.AppLogo} alt="logo" />
                    <p>
                        Edit <code>src/App.js</code> and save to reload.
                    </p>
                    <div className={s.BlockLink}>
                        <a
                            className={s.AppLink}
                                href="https://reactjs.org"
                                target="_blank"
                                rel="noopener noreferrer"
                            >
                            Learn React
                        </a>
                    </div>
                </div>
                
                {data.action.name === 'dropUp' &&
                    <p className={s.btnColor} onClick={data.action}>{data.text}</p>
                }

            </div>
        </>
    )
}

const AppView =() => {

    let [isDown, setEditDown] = useState(false);
    let [source, setEditSource] = useState(true);

    const dropdown = () => {
        setEditDown(true);
    }

    const dropUp = () => {
        setEditDown(false);
    }
    const drop = () => {
        setEditSource(false);
        setEditDown(true);
    }

    return (
        <>
            {source && 
                <p>При запуске команды <b>nmp start</b> появится новое приложение React: &nbsp;&nbsp;<span className={s.btnColor} onClick={drop}>&gt;&gt;</span> </p>
            }
            {!isDown && !source &&
                
                <Fragment>
                    {unhide({action: dropdown, style: `${s.downOut}`, text: String.fromCodePoint(62, 62)})}
                </Fragment>
            }
            {isDown && !source &&
                <Fragment>
                    {unhide({action: dropUp, style: `${s.growDown}`, text: 'Скрыть'})}
                </Fragment>
            }
        </>
    )
}

export default AppView