let strHeaderMainIntro =`
import React from 'react';
import s from './Header.module.css';
import Title from './Title';
import MainIntro from './MainIntro';


const Header = (props) => {
    return (
        <div className={s.main}>
            <Title/>
            <MainIntro/>
            <div>Question</div>
        </div>
    )
}

export default Header;;
`

