import React from 'react';
import s from './StepEleven.module.css';
import StorePackageJson from './StorePackageJson/StorePackageJson';


const StepTen = () => {
    return (
        <>
            <p>Делаю build для проекта</p>
            <p>Перед сборкой удалим все <b>console.log{`()`}</b>. Оставим один Флаг РФ:</p>
            <div className={`${s.background} ${s.blockCode} ${s.decor}`}>
                <div>
                    <code className={s.colorLightBlue}>console</code>
                    <code className={s.colorYellow}>.log{`(`} </code>
                    <code className={s.colorString}>"%c  РОССИЯ  ", "background: blue; color: yellow; font-size: x-large; border-left: 5px solid black; border-top: 30px solid white; border-right: 2px solid black; border-bottom: 30px solid red;"</code>
                    <code className={s.colorYellow}>{`)`} </code>
                    <code className={s.colorLightBlue}>;</code>
                    
                </div>
            </div>
            <p>Для определения "правильных" путей в итоговой сборке проекта внесем изменения в <b>package.json</b> вставим строчку</p>
            <div className={`${s.background} ${s.blockCode} ${s.decor}`}>
                <div>
                    <code className={s.colorLightBlue}>"homepage":  </code>
                    <code className={s.colorString}>"./"</code>
                    <code className={s.colorLightBlue}>,</code>
                </div>
            </div>
            <StorePackageJson/>
            <p>Команда для сборки</p>
            <div className={`${s.background} ${s.blockCode} ${s.decor}`}>
                <div>
                    <code className={s.colorBlue}>npm run build</code>
                </div>
            </div>
            <p>После сборки появится папка <b>build</b>, содержание её и есть готовый сайт, проверим, как работает, запустив в проводнике <b>index.html</b></p>
        </>
    )
}

export default StepTen;