import React, { Fragment, useState } from 'react';
import s from './ProjectTree.module.css';
import imgUrl from '../../../assets/img/Step5_tree.jpg';


let unhide = (data) => {
    return (
        <>

            <p>Дерево проекта получилось следующим: &nbsp;&nbsp; <span className={s.btnColor} onClick={data.action}> &gt;&gt; </span></p>
            <div className={s.container}>
                <div className={s.containerImg}>
                    <img className={`${s.modal} ${data.style}`} alt="Components" src={imgUrl} onClick={data.action}/>
                </div>
                {data.action.name === 'dropUp' &&
                        <p className={s.btnColor} onClick={data.action}>Скрыть </p>
                }
            </div>
        </>
    )
}

const ProjectTree = () => {
    let [isDown, setEditDown] = useState(false);
    let [source, setEditSource] = useState(true);

    const dropdown = () => {
        setEditDown(true);
    }

    const dropUp = () => {
        setEditDown(false);
    }
    const drop = () => {
        setEditSource(false);
        setEditDown(true);
    }

    return (
        <div>
            {source &&
                <p>Дерево проекта получилось следующим: &nbsp;&nbsp; <span className={s.btnColor} onClick={drop}> &gt;&gt; </span> </p>
            }
            {!isDown && !source &&
                <Fragment>
                    {unhide({action: dropdown, style: `${s.downOut}`, text: String.fromCodePoint(62, 62)})}
                </Fragment>
            }
            {isDown && !source &&
                <Fragment>
                    {unhide({action: dropUp, style: `${s.growDown}`, text: 'Скрыть'})}
                </Fragment>
            }
        </div>
    )
}

export default ProjectTree;
