let strIntro =`
import React from 'react';
import s from '../Header.module.css';

const Circle = () => {
    return (
        <div className={s.circle}>
            <div className={s.circle__target} key='1'/>
            <div className={s.circle__target} key='2'/>
            <div className={s.circle__target} key='3'/>
            <div className={s.circle__target} key='4'/>
        </div>
    )
}

export default Circle;
`
export default strIntro;
