import React from 'react';
import s from './StepNine.module.css';
import PrototypeSiteComponent9 from '../PrototypeSite/PrototypeSiteComponent9/PrototypeSiteComponent9';
import StoryOptions from './StoryOptions/StoryOptions';


const StepNine = () => {
    return (
        <>

            <p>Сейчас радиокнопка с вариантами вопросов остается с точкой внутри от последнего выбора, сделаем так, чтобы с новым вопросом все радиокнопки были без точек.</p>
            <p>Для этого в компоненту <b>Options</b> надо добавить <b>useEffect</b></p>
            <div className={`${s.background} ${s.blockCode} ${s.decor}`}>
                <div>
                    <code className={s.colorYellow}>useEffect  </code>
                    <code className={s.colorPurple}>{`(`}</code>
                    <code className={s.colorBlue}>{`() `}</code>
                    <code className={s.colorBlue}>{`{`} </code>
                    <code className={s.colorBlue}>{`=> `} </code>
                    <code className={s.colorBlue}>{`{`} </code>
                </div>
                <div className={s.blockCodeColumn}>
                    <div>
                        <code className={s.colorPurple}>if</code>
                        <code className={s.colorYellow}> {`(`}</code>
                        <code className={s.colorLightBlue}> props.</code>
                        <code className={s.colorYellow}>getGo</code>
                        <code className={s.colorPurple}>{`()`}</code>
                        <code className={s.colorYellow}>{` ) {`}</code>
                    </div>
                    <div className={s.blockCodeColumn}>
                        <div>
                            <code className={s.colorBlue}>let</code>
                            <code className={s.colorLightBlue}> inputOptions = document.</code>
                            <code className={s.colorYellow}>querySelectorAll</code>
                            <code className={s.colorPurple}> {`(`}</code>
                            <code className={s.colorString}>".question-options"</code>
                            <code className={s.colorYellow}>{`)`}</code>
                            <code className={s.colorLightBlue}>;</code>
                        </div>

                        <div>
                            <code className={s.colorPurple}>for</code>
                            <code className={s.colorPurple}>{`(`}</code>
                            <code className={s.colorBlue}>let </code>
                            <code className={s.colorLightBlue}>i=0; i {`<`} inputOptions.</code>
                            <code className={s.colorBlue}>length</code>
                            <code className={s.colorLightBlue}>; i++</code>
                            <code className={s.colorPurple}>{`) {`}</code>
                        </div>
                        <div className={s.blockCodeColumn}>
                            <div>
                                <code className={s.colorPurple}>if</code>
                                <code className={s.colorBlue}>{`(`}</code>
                                <code className={s.colorBlue}>inputOptions</code>
                                <code className={s.colorYellow}>{`[`}</code>
                                <code className={s.colorLightBlue}>{`i`}</code>
                                <code className={s.colorYellow}>{`]`}</code>
                                <code className={s.colorLightBlue}>.checked</code>
                                <code className={s.colorBlue}>{` {`}</code>
                            </div>
                            <div className={s.blockCodeColumn}>
                                <div>
                                    <code className={s.colorBlue}>inputOptions</code>
                                    <code className={s.colorYellow}>{`[`}</code>
                                    <code className={s.colorLightBlue}>{`i`}</code>
                                    <code className={s.colorYellow}>{`]`}</code>
                                    <code className={s.colorLightBlue}>.checked</code>
                                    <code className={s.colorBlue}> = </code>
                                    <code className={s.colorString}> ''</code>
                                    <code className={s.colorLightBlue}>;</code>
                                </div>
                            </div>
                            <code className={s.colorBlue}>{`}`}</code>
                        </div>
                        <code className={s.colorPurple}>{`}`}</code>
                    </div>
                    <code className={s.colorYellow}>{`}`}</code>
                </div>
                <div>
                    <code className={s.colorBlue}>{`}`}</code>
                    <code className={s.colorPurple}>{`)`}</code>
                </div>
            </div>
            <StoryOptions/>
            <PrototypeSiteComponent9/>
        </>
    )
}

export default StepNine;
