let strStoreIndex =`
import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import store from './redus/state';

const root = ReactDOM.createRoot(document.getElementById('root'));

let renderEntireTree = (props) => {
	console.log(props);
	root.render(
		<React.StrictMode>
			<App state={props}
        versionAnswer={store.versionAnswer.bind(store)}
        addAnswer={store.addAnswer.bind(store)}/>
		</React.StrictMode>
	);
}
renderEntireTree(store.state);

store.subscribe(renderEntireTree)
`
export default strStoreIndex;
