import React, { Fragment } from 'react';

let fragmentIndex = (
<Fragment>
<div className="app-wrapper">
    <div className="main">
		<div className="title">
			<h1>Тест</h1>
		</div>
		<div className="main-intro">
			<div className="main-intro__logo">
				<img className="logo" src= {require('../../../assets/img/html.png').default} alt="logo"/>
			</div>
			<div className="main-intro__text">
				<h2>Тест по HTML</h2>
				<div className="main-intro__description">
				<p>Описание<strong> 20 вопросов по основам HTML.</strong> При очередном прохождении теста многие вопросы
					будут заменены. За каждый правильный ответ вы получаете 1 балл, в конце каждого теста вы получаете свой
					общий балл.</p>
				</div>
			</div>
		</div>
		<div className="main-question">
			<div className="main-question__quantity">
					Вопрос
				<span>&nbsp;1&nbsp;</span>
				&nbsp;из&nbsp;
				<span>
				&nbsp;20
				</span>
			</div>
			<div className="circle">
				<div className="circle__target"></div>
				<div className="circle__target"></div>
				<div className="circle__target"></div>
				<div className="circle__target"></div>
				<div className="circle__target"></div>
				<div className="circle__target"></div>
			</div>
			<div className="circle-gadget">
				<div className="circle-gadget__target"> 1 </div>
				<div className="circle-gadget__target"> 2 </div>
			</div>
			<div className="main-question__text">
				<p>
					<span>1.</span>
					<span>&nbsp;Какое расширение должны иметь HTML документы?</span>
				</p>
			</div>
		</div>
  	</div>
	<ul className="answers">
		<li>
			<input id="quest05" name="question" type="radio"/>
			<label htmlFor="quest05">.html или .htm </label>
		</li>
		<li>
			<input id="quest15" name="question" type="radio"/>
			<label htmlFor="quest15">.txt или .doc</label>
		</li>
		<li>
			<input id="quest25" name="question" type="radio"/>
			<label htmlFor="quest25">.php или .asp</label>
		</li>
		<li>
			<input id="quest35" name="question" type="radio"/>
			<label htmlFor="quest35">.doc</label>
		</li>
	</ul>
      <a href="#/" className="button next-question" role="button">Дальше »</a>
</div>
</Fragment>

)
export default fragmentIndex;
