let strHeaderCSS = `
.main {
    display: flex;
    flex-direction: column;
    margin-top: 2%;
    font-size: 1.2em;
}
.title {
    margin: 0 2% 2%;
}
.mainIntro {
    display: flex;
    justify-content: space-between;
    flex: 30%;
    margin-top: 2%;
    padding: 1% 2%;
}
.mainIntro__logo {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    max-height: 128px;
}
.mainIntro__logo > .logo {
    max-width: 128px;
    max-height: 128px;
    width: auto;
}
.mainIntro__text {
    flex-grow: 2.5;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 1% 2%;
}
.mainQuestion {
    padding: 2%;
}
.mainQuestion__quantity {
    margin-bottom: 1%;
}
.circle {
    display: flex;
    gap: 1%;
}
.circle__target {
    border-radius:50%;
    width: 24px;
    height: 24px;
    background-color: white;
    border:1px solid black;
}
.circle__target:nth-child(1)  {
    border-radius:50%;
    width: 24px;
    height: 24px;
    background-color: #00B28E;
    border:1px solid black;
}
.circle__target:nth-child(2)  {
    border-radius:50%;
    width: 24px;
    height: 24px;
    background-color:#00B28E;
    border:1px solid black;
}
.circle__target:nth-child(3),
.circle__target:nth-child(4),
.circle__target:nth-child(5)  {
    border-radius:50%;
    width: 24px;
    height: 24px;
    background-color: red;
    border:1px solid black;

}
.circleGadget {
    display: none;
}
.circleGadget__target:nth-child(1) {
    display: flex;
    justify-content: center;
    border-radius:50%;
    width: 24px;
    height: 24px;
    background-color: #00B28E;
    border:1px solid black;
}
.circleGadget__target:nth-child(2) {
    display: flex;
    justify-content: center;
    border-radius:50%;
    width: 24px;
    height: 24px;
    background-color: red;
    border:1px solid black;
}
@media  (max-width: 800px) {
    .appWrapper {
        overflow: scroll;
    }
    .title {
        font-size: calc(var(--index) * 1.2 );
    }
    .mainIntro {
        flex-direction: column
    }
    .mainIntro__logo > .logo {
        width: calc(var(--index) * 8 );
    }
    .mainIntro__text {
        font-size: calc(var(--index) * 1.2 );
    }
    .circle__target {
        display: none;
    }
    .circleGadget {
        display: flex;
        justify-content: space-around;
    }
}
@media (max-width: 800px) and (min-aspect-ratio: 128/128) {
    .title {
        font-size: calc(var(--index) * 1.2 );
    }
    .mainIntro {
        flex-direction: column
    }
    .mainIntro__text {
        font-size: calc(var(--index) * 1.2 );
    }
    .circle__target {
        display: none;
    }
    .circleGadget {
        display: flex;
        justify-content: space-around;
    }
}
@media  (max-width: 400px) {
    .mainIntro__text {
        font-size: 12px;
    }

}
@media (max-width: 400px) and (min-aspect-ratio: 300/128) {
    .mainIntro__text {
        font-size: 12px;
    }
}
`

export default strHeaderCSS;
