import React, { Fragment, useState } from 'react';
import s from '../PrototypeSite.module.css';
import fragmentIndex from './fragmentIndex6p1';




let unhide = (data) => {
    return (
        <>
            <p>Посмотреть верстку &nbsp;&nbsp;<span className={s.btnColor} onClick={data.action}>{data.text} </span> </p>
            <div className={s.container}>
                <div className={data.style}>
                    {fragmentIndex}
                    {data.action.name === 'dropUp' &&
                        <p className={s.btnColor +' ' + s.btnWrapper} onClick={data.action}>Скрыть </p>
                    }
                </div>
            </div>

        </>
    )
}

const PrototypeSite6p1 = () => {

    let [isDown, setEditDown] = useState(false);
    let [source, setEditSource] = useState(true);

    const dropdown = () => {
        setEditDown(true);
    }

    const dropUp = () => {
        setEditDown(false);
    }
    const drop = () => {
        setEditSource(false);
        setEditDown(true);
    }

    return (
        <>
            {source &&
                <p>Посмотреть верстку &nbsp;&nbsp;<span className={s.btnColor} onClick={drop}>&gt;&gt;</span> </p>
            }
            {!isDown && !source &&
                <Fragment>
                    {unhide({action: dropdown, style: `${s.downOut}`, text: String.fromCodePoint(62, 62)})}
                </Fragment>
            }
            {isDown && !source &&
                <Fragment>
                    {unhide({action: dropUp, style: `${s.growDown}`, text: 'Скрыть'})}
                </Fragment>
            }
        </>
    )
}

export default PrototypeSite6p1;