import React, { Fragment, useEffect, useState } from 'react';
import s from './StepThird.module.css';
import str from './strIndex';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter'
import { oneDark } from 'react-syntax-highlighter/dist/esm/styles/prism';


let unhide = (data) => {

    return (
        <>
            <p>Посмотреть содержание index.html &nbsp;&nbsp;<span className={s.btnColor} onClick={data.action}> {data.text}</span> </p>
            <div className={s.container}>
                <div  className={`${data.style} ${'code'}`}>
                    <div className='containerSyntaxhigHlighter'>
                        <SyntaxHighlighter language="jsx" style={oneDark} wrapLongLines={true}>
                            {data.code}
                        </SyntaxHighlighter>
                    </div>
                </div>
                {data.action.name === 'dropUp' &&
                    <p className={s.btnColor} onClick={data.action}>data.text = {data.text}</p>
                }
            </div>
        </>
    )
}
async function recomputeCode(generator) {
    let code;
    try {
      code = await generator;
    } catch (error) {
      console.error('There was an error in your code generator: ', error);
      const unknownCode = 'Nothing to see here, move along!';
      return unknownCode;
    }
    return code;
}

const ContentIndex = () => {
    const [isDown, setEditDown] = useState(false);
    const [source, setEditSource] = useState(true);
    const [code, setCode] = useState('');

    const dropdown = () => {
        setEditDown(true);
    }
    const dropUp = () => {
        setEditDown(false);
    }
    const drop = () => {
        setEditSource(false);
        setEditDown(true);
    }
    useEffect(
        () => {
          async function debounce() {
            if(!isDown) {
                setCode('');
            } else {
              setCode(await recomputeCode(str));
            }
          }
          debounce();
        }
    );

    return (
        <>
            {source &&
                <p>Посмотреть содержание index.html &nbsp;&nbsp;<span className={s.btnColor} onClick={drop}>&gt;&gt;</span> </p>
            }
            {!isDown && !source &&
                <Fragment>
                    {unhide({action: dropdown, style: `${s.downOut}`, text: String.fromCodePoint(62, 62)})}
                </Fragment>
            }
            {isDown && !source &&
                <Fragment>
                    {unhide({action: dropUp, style: `${s.growDown}`, text: 'Скрыть', code: code})}
                </Fragment>
            }
        </>
    )

}

export default ContentIndex;
