let NumberQueastion = `
import React from 'react';
import s from '../Header.module.css';

const NumberQueastion = () => {
    return (
        <div className={s.mainQuestion__quantity}>
            Вопрос
            <span className="current">&ensp;1</span>
            <span> из </span>
            <span className="total">20</span>
        </div>
    )
}

export default NumberQueastion;
`
export default NumberQueastion;
